<template>
  <div class="main-container">
    <div class="main-breadcrumb flex-r">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/store/product-list' }">会员管理</el-breadcrumb-item>
        <el-breadcrumb-item>{{breadcrumbTitle}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="main-content">
      <div class="search-content">
        <el-form :inline="true" :model="formInline" class="search__from" label-width="70px">
          <el-form-item label="用户名">
            <el-input size="small" v-model="formInline.name" placeholder="用户名"></el-input>
          </el-form-item>

          <el-form-item label="日期">
            <el-date-picker
              v-model="formInline.addTime"
              type="daterange"
              @change="selectTime"
              align="left"
              unlink-panels
              size="small"
              value-format="yyyy-MM-dd HH:mm:ss"
              range-separator="-"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              :default-time="['00:00:00', '23:59:59']"
              :picker-options="pickerOptions2"
            ></el-date-picker>
          </el-form-item>

          <el-form-item>
            <el-button size="small" type="primary" @click="getAttributesList(formInline)">搜索</el-button>
          </el-form-item>
        </el-form>
      </div>

      <div class>
        <el-table
          border
          v-loading="loading"
          element-loading-text="老铁别急，这就给你整上..."
          header-row-class-name="table-header--gray"
          size="small"
          :data="tableData"
          row-key="attributeId"
          style="width: 100%"
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" width="55" align="center"></el-table-column>
          <el-table-column prop="img" label="微信头像" width="80" align="center">
            <!--插入图片链接的代码-->
            <template slot-scope="scope">
              <img :src="scope.row.img" alt style="width: 50px;height: 50px">
            </template>
          </el-table-column>
          <el-table-column prop="attributeId" label="昵称" align="center"></el-table-column>
          <el-table-column prop="title" label="手机号" align="center"></el-table-column>
          <el-table-column prop="val" label="姓名" align="center"></el-table-column>
          <el-table-column prop="type" label="总补助金额" align="center"></el-table-column>
          <el-table-column prop="type" label="是否为供应商" align="center"></el-table-column>
          <el-table-column prop="type" label="团队会员数" align="center"></el-table-column>
          
          <el-table-column prop="addTime" label="注册时间" align="center"></el-table-column>
          <el-table-column label="操作" align="center" width="300">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">查看详情</el-button>
              <!-- <el-button size="mini" @click="handleDel(scope.$index, scope.row)">删除</el-button> -->
              <el-button size="mini" @click="handleBtn(1)">编辑信息</el-button>
              
              <el-button size="mini" @click="handleEdit(scope.$index, scope.row)">设置下级</el-button>
              
            </template>
          </el-table-column>
        </el-table>
        <!-- 弹出框 -->
        <el-dialog title="收货地址" :visible.sync="dialogFormVisible" :modal-append-to-body="false">
          <!-- 调整等级 -->
            <el-form :model="formInline">
              <el-form-item label="现在的等级" label-width="120px">
                <el-input v-model="formInline.name" autocomplete="off" :readonly='true'></el-input>
              </el-form-item>
              <el-form-item label="选择等级" label-width="120px">
                <el-select v-model="formInline.region" placeholder="请选择活动区域">
                  <el-option label="区域一" value="shanghai"></el-option>
                  <el-option label="区域二" value="beijing"></el-option>
                </el-select>
              </el-form-item>
            </el-form>
            <!-- 调整余额 -->
            <el-form :model="formInline">
              <el-form-item label="现在的余额" label-width="120px">
                <el-input v-model="formInline.name" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label="活动区域" label-width="120px">
                <el-button type="primary">主要按钮</el-button>
                <el-button type="success">成功按钮</el-button>
              </el-form-item>
              <el-form-item label="" label-width="120px">
                <el-input v-model="formInline.name" autocomplete="off"></el-input>
              </el-form-item>
            </el-form>
            <!-- 调整推荐关系 -->
            <el-form :model="formInline">
              <el-form-item label="当前推荐用户（上级）" label-width="120px">
                <el-input v-model="formInline.name" autocomplete="off" :readonly='true'></el-input>
              </el-form-item>
              <el-form-item label="设置新的推荐用户（上级）：" label-width="120px">
                <el-select v-model="formInline.region" placeholder="请选择活动区域">
                  <el-option label="区域一" value="shanghai"></el-option>
                  <el-option label="区域二" value="beijing"></el-option>
                </el-select>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
              <el-button @click="dialogFormVisible = false">取 消</el-button>
              <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
            </div>
        </el-dialog>
        <div class="pagination text-align-right">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="current"
            :page-sizes="[15, 30, 45, 60]"
            :page-size="15"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
          ></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Sortable from "sortablejs";
import {
  APPKEY,
  APPSECRET,
  shopBaseUrl,
  attributesListUrl,
  attributesIMforUrl
} from "../../../api/api";
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return {
      formInline: {
        name: "",
        productCategory: "",
        state: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      dialogTableVisible: false,
      dialogFormVisible: false,
      addTime: "",
      tableData: [],
      breadcrumbTitle: "代理商列表",
      pickerOptions2: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      },
      active: 0,
      current: 1,
      pageSize: 15,
      multipleSelection: "",
      total: 0,
      loading: true
    };
  },
  //计算属性
  computed: {},
  watch: {},
  created() {},
  mounted() {
    this.getAttributesList();
  },
  //keep-alive 组件激活时调用
  activated() {},
  //keep-alive 组件停用时调用。
  deactivated() {},
  //方法
  methods: {
    
    handleBtn(type) {
      this.dialogFormVisible = true;
      
    },

    // 获取列表
    getAttributesList() {
      this.loading = true;

      this.$ajax({
        method: "get",
        url: attributesListUrl,
        params: {
          page: this.current - 1,
          size: this.pageSize
        }
      })
        .then(res => {
          if (res.data.code === 0) {
            this.tableData = [];
          } else {
            this.tableData = res.data.content;
            this.total = res.data.totalElements;
          }
          this.loading = false;
        })
        .catch(error => {
          console.log(error);
        });
    },
    selectTime() {
      console.log(this.formInline.addTime.join(","));
    },

    handleSizeChange(val) {
      this.loading = true;
      this.pageSize = val;
      this.getAttributesList();
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.current = val;

      this.getAttributesList();
      console.log(`当前页: ${val}`);
    },
    handleEdit(index, row) {
      console.log(index, row.attributeId);
      this.$router.push({
        path: "/store/attribute-add",
        query: {
          id: row.attributeId
        }
      });
    },
    handleDel(index, row) {
      this.$ajax({
        method: "delete",
        url: attributesIMforUrl + row.attributeId,
        params: {
          appKey: APPKEY,
          appSecret: APPSECRET
        }
      })
        .then(res => {
          if (res.status === 200) {
            this.$message.success("删除成功！");
            this.getAttributesList();
          }
        })
        .catch(error => {
          console.log(error.response);
          this.$message.error("删除失败！");
        });
    },
    handleSelectionChange(val) {
      console.log(val);
      let productId = [];
      if (val.length != 0) {
        for (let key in val) {
          productId.push(val[key].productId);
          this.multipleSelection = productId.join(",");
        }
      } else {
        this.multipleSelection = "";
      }

      console.log(this.multipleSelection);
    }
  }
};
</script>
<style scoped lang="scss">
.search-content {
  background: #f8f8f8;
  padding: 15px 0 0 15px;
  margin-bottom: 25px;
}

.tab-click {
  border-bottom: 2px solid #fff;
  margin-bottom: -2px;
  color: #4170ff;
}
.btns {
  padding: 19px;
  border-bottom: 1px solid #ddd;
  border-left: 1px solid #ddd;
}
</style>