<template>
  <div class="main-container">
    <div class="main-breadcrumb flex-r">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="{ path: '/store/classify' }">客户管理</el-breadcrumb-item>
        <el-breadcrumb-item>{{breadcrumbTitle}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div class="main-content">
      <!-- <p style="padding:0 0 10px 0;">{{breadcrumbTitle}}</p> -->
      <el-form
        :model="goodsinfo"
        :rules="ruleInfo"
        ref="goodsInfo"
        label-width="120px"
        class="demo-ruleForm"
      >
        <el-form-item label="公司名称：" prop="name">
          <el-input v-model="goodsinfo.name" style="width:372px;"></el-input>
        </el-form-item>
        <el-form-item label="状态：" prop="name">
            <el-switch
                v-model="goodsinfo.ifShow"
                active-color="#13ce66"
                inactive-color="#ff4949">
            </el-switch>
        </el-form-item>
        
        
        <el-form-item label="图片描述：" prop="unit">
          <el-input type="textarea" v-model="goodsinfo.unit" style="width:372px;"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button @click="handleSubmit('goodsInfo')">提交</el-button>
        </el-form-item>
        
      </el-form>
    </div>
  </div>
</template>
<script>
import {
  APPKEY,
  APPSECRET,
  qiniuTokenUrl,
  qiniuKeyUrl,
  productsUrl,
  deliveryTemplatesUrl,
  categoryIMforUrl,
  categoryUrl
} from "../../../api/api";
import { findElem } from "../../../utils/fn";
import $ from "jquery";
export default {
  name: "",
  components: {},
  // props: {},
  data() {
    return {
      breadcrumbTitle: "新增客户",
      imageUrl: "",
      upImgUrl: "",
      upImgUrlArr: [],
      radio: 1,
      saveData: {},
      size: 100,
      value: "",
      tabOptions: [],
      deliveryTemplates: [],

      options: [],
      categoryData: [],
      //parentId: null,//保存父级分类的id
      childTypeData: [],
      //childId: null, //子级id
      childTypeData2: [],
      //childId: null, //三子级id
      dialogImageUrl: "",
      dialogVisible: false,
      goodsinfo: {
        type: "",
        parentId: null, //保存父级分类id
        childId: null, //保存子级分类id
        childId2: null, //保存三子级分类id
        name: "",
        no: 0,
        unit: "台",
        ifShow: true,
        price: 0,
        costPrice: 0,
        quantity: 0,
        ifRent: "true",
        options: [],
        ifAgency: "1",
        unit: "",
        deposit: null,
        description: "",
        productDetail: "",
        region: "",
        delivery: false,
        resource: "1",
        desc: "",
        postFee: "8",
        number: "",
        weight: 0,
        linePrice: 0,
        warnQuantity: 2,
        pointRate: "",
        img: "",
        content: "",
        productCategoryId: "",
        postType: "",
        // productSkus: [],
        productCategoryExtends: [
          {
            fieldKey: "",
            fieldName: "",
            fieldValue: ""
          }
        ]
      },
      userinfo: JSON.parse(sessionStorage.getItem("userInfo")),
      skuDataList: [],
      skuName: null,
      imgName: "",
      visible: false,
      uploadList: [], //缩略图
      uploadList3: [],
      addImgRange: "",
      ruleInfo: {
        name: [{ required: true, message: "请输入分类名称", trigger: "blur" }],
        unit: [{ required: true, message: "请输入计量单位", trigger: "blur" }]
      },
      // 注意此项为数组 type Array
      originalPrices: [
        {
          price: 0,
          quantity: 0,
          number: "",
          img: ""
        }
      ],
      enableSpec: null,
      // 注意此项为数组 type Array
      originalPrices: [
        {
          price: 0,
          quantity: 0,
          number: "",
          img: ""
        }
      ],
      // from backend
      specs: [],
      // from backend
      specPrices: [],
      newSpecName: ["", ""],
      tbIndex: null, //选择上传图片的index
      //商品扩展字段-------------------
      proExtend: [
        {
          fieldKey: "",
          fieldName: [],
          fieldValue: []
        }
      ],
      extendName: [],
      //编辑操作数据-----
      productData: "", //商品详细数据
      productSkus: "", //获取商品详情的sku
      skusArr: [],
      qnKey: "",
      qnToken: "",
      value: [],
      list: [],
      loading: false,
      states: [
        "Alabama",
        "Alaska",
        "Arizona",
        "Arkansas",
        "California",
        "Colorado",
        "Connecticut",
        "Delaware",
        "Florida",
        "Georgia",
        "Hawaii",
        "Idaho",
        "Illinois",
        "Indiana",
        "Iowa",
        "Kansas",
        "Kentucky",
        "Louisiana",
        "Maine",
        "Maryland",
        "Massachusetts",
        "Michigan",
        "Minnesota",
        "Mississippi",
        "Missouri",
        "Montana",
        "Nebraska",
        "Nevada",
        "New Hampshire",
        "New Jersey",
        "New Mexico",
        "New York",
        "North Carolina",
        "North Dakota",
        "Ohio",
        "Oklahoma",
        "Oregon",
        "Pennsylvania",
        "Rhode Island",
        "South Carolina",
        "South Dakota",
        "Tennessee",
        "Texas",
        "Utah",
        "Vermont",
        "Virginia",
        "Washington",
        "West Virginia",
        "Wisconsin",
        "Wyoming"
      ]
    };
  },
  //计算属性
  computed: {
    displayedPrices() {
      return this.enableSpec ? this.defaultAddPrices : this.originalPrices[0];
    },
    tableData() {
      let arr = this.specPrices;
      for (let i = 0; i < arr.length; i++) {
        arr[i].spec0 = arr[i].specs[0];
        arr[i].spec1 = arr[i].specs[1];
      }
      return arr;
    }
  },
  watch: {},
  created() {
    this.getCategoryList();
    this.specs = [];
    this.specPrices = [];

    if (this.specs.length === 0) {
      // 初始化规格数据
      let obj = {};
      obj.type = "";
      obj.children = [];
      this.specs.push(obj);
      // 初始化价格数据
      let _obj = [{}];
      _obj[0].specs = [""];
      _obj[0].prices = {
        price: 0,
        quantity: 0,
        number: "",
        img: "",
        no: "",
        weight: "",
        linePrice: ""
      };
      this.specPrices = _obj;
    }
    this.enableSpec = true;
  },
  mounted() {
    this.getPrevImfor();
    this.uploadList = this.$refs.upload.fileList;
    this.list = this.states.map(item => {
      return { value: item, label: item };
    });
  },
  //keep-alive 组件激活时调用
  activated() {},
  //keep-alive 组件停用时调用。
  deactivated() {},
  //方法
  methods: {
    //   获取商品信息
    getPrevImfor() {
      if (this.$route.query.id) {
        this.breadcrumbTitle = "编辑商品分类";
        this.$ajax({
          method: "get",
          url: categoryIMforUrl + "/" + this.$route.query.id,
          params: {
            appKey: APPKEY,
            appSecret: APPSECRET,
            sort: "sequence,asc"
          },
          headers: {
            "Content-Type": "application/x-www-form-urlencoded"
          }
        })
          .then(res => {
            if (res.status === 200) {
              let data = res.data.content;
              console.log(data);
              // this.goodsinfo.parentId =
              //   data.productCategory.parent.parent.productCategoryId;
              // this.getProductChildType();
              // this.goodsinfo.childId =
              //   data.productCategory.parent.productCategoryId;
              // this.getProductChildType2();
              // this.goodsinfo.childId2 = data.productCategory.productCategoryId;
              this.goodsinfo.name = data.name;
              this.goodsinfo.description = data.description;
              this.goodsinfo.unit = data.unit;
              this.goodsinfo.img = data.img;
              this.goodsinfo.ifShow = data.ifShow;
              if (data.ifShow === true) {
                this.radio = 1;
              } else {
                this.radio = 2;
              }

              let tabsValue = data.productExtends[0].fieldValue.split(",");
              console.log(tabsValue);

              let productExtendsArr = data.productExtends;

              let ifRentV = findElem(productExtendsArr, "fieldKey", "ifRent");
              let depositV = findElem(productExtendsArr, "fieldKey", "deposit");
              if (ifRentV === -1) {
                this.goodsinfo.ifRent = "true";
                if (depositV === -1) {
                  this.goodsinfo.deposit = 0;
                } else {
                  this.goodsinfo.deposit = Number(
                    productExtendsArr[depositV].fieldValue
                  );
                }
              } else {
                this.goodsinfo.ifRent = productExtendsArr[ifRentV].fieldValue;

                if (this.goodsinfo.ifRent == "true") {
                  if (depositV === -1) {
                    this.goodsinfo.deposit = 0;
                  } else {
                    this.goodsinfo.deposit = Number(
                      productExtendsArr[depositV].fieldValue
                    );
                  }
                } else {
                  this.goodsinfo.deposit = null;
                }
              }
              //商品缩略图
              let obj = {
                name: data.img,
                status: "finished",
                showProgress: false,
                url: data.img
              };
              this.uploadList.push(obj);
            } else {
              this.$message.error("数据获取失败");
            }
          })
          .catch(error => {
            console.log(error);
          });
      }
    },
    radioChange() {
      if (this.radio === 1) {
        this.goodsinfo.ifShow = true;
      } else {
        this.goodsinfo.ifShow = false;
      }
      console.log(this.goodsinfo.ifShow);
    },
    // 第一级选项
    getCategoryList() {
      this.$ajax({
        method: "get",
        url: deliveryTemplatesUrl,
        params: {
          appKey: APPKEY,
          appSecret: APPSECRET,
          sort: "sequence,asc"
        }
      })
        .then(res => {
          if (res.data.code === 0) {
            this.categoryData = [];
          } else {
            let data = res.data;
            for (let i = 0; i < data.length; i++) {
              let parentObj = {
                value: data[i].productCategoryId,
                label: data[i].name
              };
              if (data[i].sons.length != 0) {
                parentObj.children = [];
                for (let j = 0; j < data[i].sons.length; j++) {
                  let childObj = {
                    value: data[i].sons[j].productCategoryId,
                    label: data[i].sons[j].name
                  };
                  if (data[i].sons[j].sons.length != 0) {
                    childObj.children = [];
                    for (let k = 0; k < data[i].sons[j].sons.length; k++) {
                      let childObj1 = {
                        value: data[i].sons[j].sons[k].productCategoryId,
                        label: data[i].sons[j].sons[k].name
                      };
                      childObj.children.push(childObj1);
                    }
                  }
                  parentObj.children.push(childObj);
                }
              }
              this.categoryData.push(parentObj);
            }
          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    selectCategory() {
      if (this.goodsinfo.options.length === 3) {
        this.goodsinfo.parentId = this.goodsinfo.options[2];
      } else if (this.goodsinfo.options.length === 2) {
        this.goodsinfo.parentId = this.goodsinfo.options[1];
      } else {
        this.goodsinfo.parentId = this.goodsinfo.options[0];
      }
      console.log(this.goodsinfo.parentId);
    },
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          if (this.uploadList.length === 0) {
            return this.$message.success("请上传分类图片");
          }
          let obj = {};
          obj.appKey = APPKEY;
          obj.appSecret = APPSECRET;
          obj.shopId = 102;
          obj.sequence = 0;
          obj.description = this.goodsinfo.description;
          obj.parentId = this.goodsinfo.parentId;
          obj.name = this.goodsinfo.name;
          obj.img = this.uploadList[0].url;
          obj.ifShow = this.goodsinfo.ifShow;
          obj.unit = this.goodsinfo.unit;
          obj.productCategoryExtends = this.goodsinfo.productCategoryExtends;
          // if (this.goodsinfo.childId === null || "") {
          //   obj.productCategoryId = this.goodsinfo.parentId;
          // } else if (this.goodsinfo.childId2 === null || "") {
          //   obj.productCategoryId = this.goodsinfo.childId;
          // } else {
          //   obj.productCategoryId = this.goodsinfo.childId2;
          // }
          this.postgoods(obj);
        } else {
          this.$message.error("请将信息填写完整!");
        }
      });
    },
    //添加关联属性
    addType() {
      let obj = {};

      obj.fieldName = "";
      obj.fieldKey = obj.fieldName;
      obj.fieldValue = "";
      if (
        this.goodsinfo.productCategoryExtends[0].fieldName != "" &&
        this.goodsinfo.productCategoryExtends[0].fieldValue != ""
      ) {
        this.goodsinfo.productCategoryExtends.push(obj);
      }
    },
    //sku上传图片状态
    beforeAvatarUpload(file) {
      // const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      // if (!isJPG) {
        // this.$message.error("上传头像图片只能是 JPG 格式!");
      // }
      if (!isLt2M) {
        this.$message.error("上传图片大小不能超过 2MB!");
      }
      // return isJPG && isLt2M;
      return isLt2M;
    },
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
      let img = "https://qiniu.easyapi.com/" + res.key;
      file.url = img;
      file.name = file.name;
      this.upImgUrl = img;
      console.log(this.upImgUrl);
    },
    imgsuccess(res, file, fileList) {
      this.specPrices[this.tbIndex].prices.img =
        "http://qiniu.easyapi.com/" + res.key;
    },
    //上传图片
    handleView(url) {
      this.imgName = url;
      this.visible = true;
    },
    handleFormatError(file) {
      this.$Notice.warning({
        title: "文件格式错误",
        desc: "文件 " + file.name + "格式不正确, 请上传 jpg 或者 png格式图片."
      });
    },
    handleSuccess(res, file) {
      // this.imageUrl = URL.createObjectURL(file.raw);
      let img = "https://qiniu.easyapi.com/" + res.key;
      this.goodsinfo.img = img;
      file.url = this.goodsinfo.img;
      this.upImgUrlArr.push(img);
      this.uploadList.push(file);
    },
    //获取七牛token,key,
    getTokenAandKey(tbRow) {
      if (tbRow) {
        this.tbIndex = tbRow.$index;
      }
      this.getqiniuToken();
      this.getqiniukey();
    },
    getqiniuToken() {
      this.$ajax({
        method: "get",
        url: qiniuTokenUrl
      })
        .then(res => {
          this.qnToken = res.data.content.upToken;
        })
        .catch(error => {
          console.error(error.response);
        });
    },
    getqiniukey() {
      this.$ajax({
        method: "get",
        url: qiniuKeyUrl
      })
        .then(res => {
          this.qnKey = res.data.content.key;
        })
        .catch(error => {
          console.log(error.response);
        });
    },
    //新建商品
    postgoods(goods) {
      this.$ajax({
        method: "post",
        url: categoryUrl,
        data: goods
      })
        .then(res => {
          if (res.status === 200) {
            this.$message({
              message: "保存成功",
              type: "success"
            });
            let postId = res.data.content.productId;
            console.log(postId);
            this.$router.push({
              path: "/store/classify",
              query: {
                id: postId
              }
            });
          }
        })
        .catch(error => {
          console.log(error);
        });
    },

    //点击图片触发事件
    imgClick() {
      this.getTokenAandKey();
      $("#upload").click();
    },

    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    remoteMethod(query) {
      if (query !== "") {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.options = this.list.filter(item => {
            return item.label.toLowerCase().indexOf(query.toLowerCase()) > -1;
          });
        }, 200);
      } else {
        this.options = [];
      }
    }
  }
};
</script>
